.flex {
    display: flex;
  
    &-row {
      flex-direction: row;
    }
  
    &-column {
      flex-direction: column;
    }
  
    &-space-between {
      justify-content: space-between;
    }
  
    &-space-around {
      justify-content: space-around;
    }
  
    &-center {
      justify-content: center;
    }
  }
  