.search {
  flex-wrap: unset;
  margin: 0px 16px 0px 16px;
  max-height: 288px;
  overflow-x: hidden;
  overflow-y: scroll;
  position: absolute;
  background-color: white;
  top: 100%;
  z-index: 100;
}

.search-result {
  max-width: 80%;
  padding: 8px 8px;

  &:hover {
    background-color: orange;
    cursor: pointer;
  }
}

.input-default {
  background: transparent;
  border: 0;
  border-bottom: 2px solid black;
  margin-top: 16px;
  outline: 0;
  width: 100%;
}
