.home {
  padding: 16px 16px;
}

.warehouse-content {
  height: 696px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.results {
  max-width: 512px;
}

.filters {
  width: 20%;
  label {
    font-size: 1rem;
  }
}

.filter-header {
  font-size: 1.5rem;
  margin: 8px 0px;
}

.author-selections {
  font-size: 1.2rem;
  margin: 8px 0px;
  border: 1px solid black;

  &:hover {
    background-color: orange;
    cursor: pointer;
  }
}

.filter {
  position: relative;
  margin: 0px 0px 32px 0px;
}

.checkbox-wrapper {
  padding: 4px 4px;
}
.checkbox {
  margin: 4px 8px 0px 0px;
}

.select-default {
  margin: 16px 8px;
}

.information {
  display: flex;
  flex-direction: column;
  align-items: center;
}
