.login-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 70px);
  }
  
  .card {
    transition: 0.4s;
    height: 70%;
    width: 40%;
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.5);
  }
    
  .form-default{
    font-size: 1.5rem;
    padding-left: 7%;
    padding-right: 7%;
    display: flex;
    height: 75%;
    flex-direction: column;
    justify-content: space-around;
    align-self: center;
  }
  
  input[type="text"], input[type="password"]{
    padding-left: 1px;
    padding-right: 1px;
    height: 40px;
    border-radius: 5px;
    border: .5px solid rgb(143, 143, 143);
  }
  
  label{
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
  }