.nav-header {
    align-items: center;
    background-color: red;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 32px 16px;
    position: sticky;
    top: 0;
    z-index: 10;
  
    .brand {
      align-items: center;
      color: white;
      display: flex;
      font-size: 2rem;
  
      .logo {
        margin-right: 16px;
        width: 70px;
      }
    }
  }
  