.link {
  font-weight: bold;
  outline: 0;
  text-decoration: none;

  &-default,
  &-default:visited,
  &-default:active {
    color: purple;
  }

  &-default:hover {
    color: lightpink;
  }

  &-light,
  &-light:visited,
  &-light:active {
    color: white;
    padding: 0 8px;
    }

  &-light:hover {
    color: white;
  }
}
